.dropdown-menu {
  position: relative;
  position: absolute;
  top: 70px;
  left: 0;
  padding: 10px;
  width: 300px;
  border: 1px solid #cedee8;
  box-shadow: 0px 2px 10px rgba(0,0,0,0.05);
  border-radius: 6px;
  color: #323c47;
  transform-origin: top left;
  transition: 0.2s;
  transform: scale(0);
}
.dropdown-menu:before {
  position: absolute;
  display: block;
  content: '';
  top: -7px;
  left: 10px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 7px 7px 7px;
  border-color: transparent transparent #cedee8 transparent;
}
.dropdown-menu:after {
  position: absolute;
  display: block;
  content: '';
  top: -6px;
  left: 10px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 7px 7px 7px;
  border-color: transparent transparent #fff transparent;
}
.is-right .dropdown-menu {
  left: auto;
  left: initial;
  right: 0;
  transform-origin: top right;
}
.is-right .dropdown-menu:before,
.is-right .dropdown-menu:after {
  left: auto;
  left: initial;
  right: 10px;
}
.is-center .dropdown-menu {
  left: calc(50% - 150px);
  transform-origin: top center;
}
.is-center .dropdown-menu:before,
.is-center .dropdown-menu:after {
  left: calc(50% - 7px);
}
.dropdown-menu.is-active {
  transform: scale(1);
}
